import { useState } from "react";
import styles from "./Payments.module.css"
import PaymentsItems from "./PaymentsItems/PaymentsItems";
import {Test} from "./PaymentsItems/Test";

const Payments = (props) => {
    
    let paymentsItems = props.paymentsPage.paymentsData.map(payments => {
        return <PaymentsItems
            key={payments.date}
            date={payments.date}
            payments={payments.payments}
        />
    })

    let [active, setActive] = useState(false);
    let onClick = () => {
        setActive(!active);
    }

    let paymentChange = (e) => {
        let text = e.target.value;
        props.onChange(text);
    }

    let costChange = (e) => {
        let num = e.target.value;
        props.onCostChange(num);
    }

    let onAddPayment = () => {
        props.addPayments();
    }

    return (<>

        <div className={styles.items}>
            <div className={styles['button-wrap']}>
                <button onClick={onClick}>Сategory</button>
                <div className={active ? styles.active : styles.hidden}  > {/* в разрабтке */}
                    <p onClick={onClick}>Home</p>
                    <p onClick={onClick}>Car</p>
                    <p onClick={onClick}>Sport</p>
                    <p onClick={onClick}>...</p>
                </div>
            </div>

            <input                                              /* переписать на Formik */
                placeholder="product"
                value={props.paymentsPage.newPaymentText}
                onChange={(e) => paymentChange(e)}
            />
            <input 
                value={props.paymentsPage.newPaymentCost}
                onChange={costChange}
                className={styles.cost}
            /> руб. 
            <button onClick={onAddPayment}>Add</button>
            <div>Months</div>  
            {paymentsItems}
            {/* <Test /> */}  {/* Analytics */}
        </div>

    </>)
}

export default Payments;